.orders-container {
  margin: 20px;
  margin-left: 350px;
  margin-right: 350px;
  }
  
  .order-item {
    background-color: #f9f3f3;
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  }
  
  .order-item:hover {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  }
  