.products-container {
    margin: 20px;
    margin-left: 300px;
    margin-right: 300px;
    
  }
  
  .product-item {
    background-color: #efeded;
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  }
  
  .product-item:hover {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  }
  .product-item button {
    margin-left: 10px; 
  }
  
  .product-item button:first-child {
    margin-right: 10px; 
  }
  